module.exports = "<header id=\"new-header\" class=\"\">\r\n  <nav class=\"navbar navbar-default navbar-fixed-top\">\r\n    <div class=\"container-fluid nav-padding\">\r\n      <div class=\"navbar-header\">\r\n        <a [routerLink]=\"['/admin/users']\" class=\"navbar-brand navbar-logopadding\">\r\n          <img src=\"assets/images/outgrow-logo.png\" />\r\n        </a>\r\n      </div>\r\n      <div class=\"navbar-collapse collapse\" id=\"navbar\">\r\n        <div class=\"navbar-left col-md-7 np\">\r\n          <ul *ngIf=\"loggedIn.isLoggedIn\">\r\n            <li><a href=\"\" class=\"hvr-underline-from-left\" [routerLink]=\"['/admin']\"><i class=\"material-icons\">home</i> &nbsp;Admin</a></li>\r\n          </ul>\r\n          <ul *ngIf=\"!loggedIn.isLoggedIn\">\r\n            <li><a [routerLink]=\"['/']\"><i class=\"material-icons\">home</i> &nbsp;Home</a></li>\r\n          </ul>\r\n        </div>\r\n        <div class=\"navbar-right\">\r\n          <div class=\"col-xs-12 navbar-right-block\">\r\n            <!-- <a href=\"\" class=\"navbar-name\">\r\n                                <i class=\"material-icons\">account_box</i>\r\n                                <span class=\"ellipsis\"> &nbsp;Pratham</span>\r\n                            </a> -->\r\n\r\n\r\n            <!-- Start: header name-dropdown -->\r\n            <div class=\"btn-group name-dropdown-wrapper name-dd-minh\" *ngIf=\"!loggedIn.isLoggedIn\">\r\n              <button type=\"button\" class=\"btn btn-default dropdown-toggle\" data-target=\"#login\" data-toggle=\"modal\">\r\n                <i class=\"material-icons\">account_box</i>\r\n                <span class=\"ellipsis name-title\"> &nbsp;Login</span>\r\n              </button>\r\n            </div>\r\n            <div class=\"btn-group name-dropdown-wrapper name-dd-minh\" *ngIf=\"loggedIn.isLoggedIn\">\r\n              <button type=\"button\" class=\"btn btn-default dropdown-toggle\" data-toggle=\"dropdown\" aria-haspopup=\"true\" aria-expanded=\"false\">\r\n                <i class=\"material-icons\">account_box</i>\r\n                <span class=\"ellipsis name-title\"> &nbsp;{{username}}</span>\r\n              </button>\r\n              <ul class=\"dropdown-menu\">\r\n                <div class=\"company-list\">\r\n                  <li class=\"active\" *ngFor=\"let company of myCompanies\">\r\n                    <a href=\"#\" class=\"hvr-sweep-to-right\">\r\n                      <span class=\"company-list-title\">Demo name</span>\r\n                      <!--<span class=\"company-selected\"><i class=\"material-icons\">done</i></span>-->\r\n                    </a>\r\n                  </li>\r\n                    <hr class=\"name-dropdown-border\">\r\n                </div>\r\n\r\n                <div class=\"name-list\">\r\n                  <li>\r\n                    <a href=\"#\" class=\"hvr-sweep-to-right\">\r\n                      <span class=\"name-list-icon\"><i class=\"material-icons\">person</i></span>\r\n                      <span class=\"name-list-title\">My Profile</span>\r\n                    </a>\r\n                  </li>\r\n\r\n                  <li>\r\n                    <a class=\"hvr-sweep-to-right\" (click)=\"onLogout()\" href=\"javascript:void(0)\">\r\n                      <span class=\"name-list-icon\"><i class=\"material-icons\">power_settings_new</i></span>\r\n                      <span class=\"name-list-title\">Logout</span>\r\n                    </a>\r\n                  </li>\r\n                </div>\r\n              </ul>\r\n            </div>\r\n            <!-- End: header name-dropdown -->\r\n\r\n            <!-- Start: Help dropdown -->\r\n            <div class=\"btn-group help-dropdown-wrapper\">\r\n              <button type=\"button\" class=\"btn btn-default dropdown-toggle\" data-toggle=\"dropdown\" aria-haspopup=\"true\" aria-expanded=\"false\">\r\n                <i class=\"material-icons\">help_outline</i>\r\n              </button>\r\n            </div>\r\n            <!-- End: Help dropdown -->\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </nav>\r\n</header>\r\n"

